










export default {
	data(){
		return{
			title:'',
			strings:'',
			imgage:''
		}	
	},
	onLoad:function(e){
		uni.request({
			url: 'http://news-at.zhihu.com/api/2/news/'+e.newsid,
			method: 'GET',
			data: {},
			success: res => {
				this.title=res.data.title;
				this.strings=res.data.body.replace("type=“text/javascript”",'type="text/javascript"');
				this.imgage=res.data.image;
			},
			fail: () => {},
			complete: () => {}
		});
	}
}




